
body {
    font-family: 'PixelifySans', sans-serif;
    font-size: 24px;
    font-weight: 100;

    margin: 2em;

    background: var(--hex-black);
    color: var(--hex-white);
}


/* Home Page
   =================================== */
.intro {
    max-width: 40rem;
}

a.intro_email,
a.intro_email:active,
a.intro_email:visited {
    color: var(--hex-white); /* A blue color by default. You can change this to any color you prefer. */
    text-decoration: none;
    background-color: transparent;
    transition: color 0.3s ease-in-out;
    font-size: 60px
}
  
a.intro_email:hover {
    color: var(--hex-magenta);
    text-decoration: underline;
}
  
a.intro_email:focus {
    outline: thin dotted;
}


.intro_email_domain {
    color: var(--hex-magenta);
}
